@import 'mixins';
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    .App{
        .button {
            border-radius: 5px;
            transition: transform 0.5s;
            -webkit-user-select: none;       
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
        }
        .button:hover{
            transform: scale(1.02);
            box-shadow: 0 0 5px black;
        }
        .linkIcon{
            font-size: 2.5rem;
            text-decoration: none;
            color: inherit;
            transition: 0.3s;
        }
        .linkIcon:hover{
            filter:drop-shadow(0 0 1px rgba(0, 0, 0, 0.5));
        }
        // Animations
        .slideInFromLeft {
            animation: slideInFromLeft 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
        }
        .slideInFromRight{
            animation: slideInFromRight 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
        }
        @keyframes slideInFromLeft {
			0% {
				transform: translateX(-100vw);
				opacity: 0%;
			}
			100% {
				transform: translateX(0);
				opacity: 100%;
			}
		}
		@keyframes slideInFromRight {
			0% {
				transform: translateX(100vw);
				opacity: 0%;
			}
			100% {
				transform: translateX(0);
				opacity: 100%;
			}
		}
        .float{
            animation: float 5s ease-in-out infinite;
        }
        @keyframes float {
            0% {
                box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.5);
                transform: translatey(0px);
            }
            50% {
                box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.2);
                transform: translatey(-10px);
            }
            100% {
                box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.5);
                transform: translatey(0px);
            }
        }
        .floatIcon{
            animation: floatIcon 5s ease-in-out infinite;
        }
        @keyframes floatIcon {
            0% {
                filter:drop-shadow(5px 10px 1px rgba(0, 0, 0, 0.4));
                transform: translatey(0px);
            }
            50% {
                filter:drop-shadow(5px 10px 3px rgba(0, 0, 0, 0.2));
                transform: translatey(-5px);
            }
            100% {
                filter:drop-shadow(5px 10px 1px rgba(0, 0, 0, 0.4));
                transform: translatey(0px);
            }
        }
    }
}
